<template>
    <div>
        <el-container id="el-containerHome">
            <el-aside :hidden="adminHomeCfg.asideHidden" width="250px">
                <h1 style="text-align: center; color: #33CABB;">后台管理系统</h1>
                <el-divider></el-divider>
                <el-menu unique-opened router
                         default-active="welcomeAdmin"
                         class="el-menu-aside"
                         @select="asideMenu_Select"
                         text-color="#5E5E5E"
                         active-text-color="#33CABB">
                    <el-menu-item index="welcomeAdmin">
                        <i class="el-icon-s-home"></i>
                        <span slot="title">系统首页</span>
                    </el-menu-item>
                    <el-submenu index="userManage">
                        <template slot="title">
                            <i class="el-icon-user-solid"></i>
                            <span>用户信息管理</span>
                        </template>
                        <el-menu-item index="userInfo">用户信息</el-menu-item>
                        <el-menu-item index="bankCard">银行卡</el-menu-item>
                        <el-menu-item index="userCredit">个人征信</el-menu-item>
                    </el-submenu>

                    <el-submenu index="financialProductManage">
                        <template slot="title">
                            <i class="el-icon-s-goods"></i>
                            <span>理财产品管理</span>
                        </template>
                        <el-menu-item index="changeManage">零钱理财</el-menu-item>
                        <el-menu-item index="salaryManage">工资理财</el-menu-item>
                        <el-menu-item index="termManage">期限理财</el-menu-item>
                        <el-menu-item index="fundManage">基金理财</el-menu-item>
                        <el-menu-item index="bankManage">精选银行</el-menu-item>
                    </el-submenu>

                    <el-submenu index="rightManage">
                        <template slot="title">
                            <i class="el-icon-key"></i>
                            <span>权限管理</span>
                        </template>
                        <el-menu-item index="userRight">用户权限</el-menu-item>
                        <el-menu-item index="adminRight">管理员权限</el-menu-item>
                    </el-submenu>

                    <el-submenu index="loanManage">
                        <template slot="title">
                            <i class="el-icon-s-finance"></i>
                            <span>网贷管理</span>
                        </template>
                        <el-menu-item index="loanAudit">网贷审核</el-menu-item>
                        <el-menu-item index="loanInfo">网贷信息</el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>

            <el-container>
                <!--头部信息栏-->
                <el-header style="padding: 0px">
                    <el-row>
                        <el-col :span="5">
                            <h1 style="color: #33CABB;">
                                <el-button size="mini" icon="el-icon-s-unfold" class="el-button1" @click="adminHomeCfg.asideHidden = !adminHomeCfg.asideHidden"></el-button>
                                {{adminHomeCfg.title}}
                            </h1>
                        </el-col>
                        <el-col :span="2" :offset="17">
                            <el-menu class="el-menu-user" mode="horizontal" @select="userMenu_Select">
                                <el-submenu index="User">
                                    <template slot="title">
                                        <el-avatar :src="this.$store.state.userInfo.picture"></el-avatar>
                                    </template>
                                    <el-menu-item index="userMenu_Type" disabled>类型：{{this.$store.state.userInfo.type}}</el-menu-item>
                                    <el-menu-item index="userMenu_Name" disabled>昵称：{{this.$store.state.userInfo.username}}</el-menu-item>
                                    <el-menu-item index="userMenu_ID" disabled>ID:{{this.$store.state.userInfo.id}}</el-menu-item>
                                    <el-menu-item index="userMenu_Information">用户中心</el-menu-item>
                                    <el-menu-item index="userMenu_Exit">退出</el-menu-item>
                                </el-submenu>
                            </el-menu>
                        </el-col>
                    </el-row>
                </el-header>
                <el-main>
                    <!--路由占位符-->
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "adminHome",
        data() {
            return {
                adminHomeCfg: {
                    title: "系统首页",
                    asideHidden: false,
                },
            }
        },
        created() {
            this.$router.replace('/welcomeAdmin');
        },
        methods: {
            userMenu_Select(key) {
                switch (key) {
                    case 'userMenu_Information':
                        console.log('你点击了用户中心');
                        this.$router.replace('/user/userCenter');
                        break;
                    case 'userMenu_Exit':
                        console.log('你点击了注销');
                        this.$store.commit('userExit'); // 清除登录状态
                        this.$router.replace('/Login');
                        break;
                    default:
                }
            },
            asideMenu_Select(key) {
                switch (key) {
                    // 用户信息管理
                    case 'welcomeAdmin':
                        console.log('你点击了系统首页');
                        this.adminHomeCfg.title = '系统首页';
                        break;
                    case 'userInfo':
                        console.log('你点击了用户信息');
                        this.adminHomeCfg.title = '用户信息';
                        break;
                    case 'bankCard':
                        console.log('你点击了银行卡');
                        this.adminHomeCfg.title = '银行卡';
                        break;
                    case 'userCredit':
                        console.log('你点击了个人征信');
                        this.adminHomeCfg.title = '个人征信';
                        break;
                    // 理财产品管理
                    case 'changeManage':
                        console.log('你点击了零钱理财');
                        this.adminHomeCfg.title = '零钱理财';
                        break;
                    case 'salaryManage':
                        console.log('你点击了工资理财');
                        this.adminHomeCfg.title = '工资理财';
                        break;
                    case 'termManage':
                        console.log('你点击了期限理财');
                        this.adminHomeCfg.title = '期限理财';
                        break;
                    case 'fundManage':
                        console.log('你点击了基金理财');
                        this.adminHomeCfg.title = '基金理财';
                        break;
                    case 'bankManage':
                        console.log('你点击了精选银行');
                        this.adminHomeCfg.title = '精选银行';
                        break;
                    // 权限管理
                    case 'userRight':
                        console.log('你点击了用户权限');
                        this.adminHomeCfg.title = '用户权限';
                        break;
                    case 'adminRight':
                        console.log('你点击了管理员权限');
                        this.adminHomeCfg.title = '管理员权限';
                        break;
                    // 网贷管理
                    case 'loanAudit':
                        console.log('你点击了网贷审核');
                        this.adminHomeCfg.title = '网贷审核';
                        break;
                    case 'loanInfo':
                        console.log('你点击了网贷信息');
                        this.adminHomeCfg.title = '网贷信息';
                        break;
                    default:
                }
            },
        },
    }
</script>

<style lang="less" scoped>
    .baseStyle() {
        background-color: rgba(255,255,255,0.5);
    }
    #el-containerHome {
        height: 100%;
        .el-aside {
            height: 100%;
            .baseStyle();
        }
        .el-header {
            .baseStyle();
            .el-button1 {
                border-style: none;
            }
        }
        .el-main {
        }
    }
</style>
