import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from "../views/Login";
import Register from "../views/Register";

// 管理员页面
import adminHome from "../views/admin/adminHome";
import welcomeAdmin from "../views/admin/welcomeAdmin";
// 理财产品管理子页面
import bankManage from "../views/admin/financialProductManage/bankManage";
import changeManage from "../views/admin/financialProductManage/changeManage";
import fundManage from "../views/admin/financialProductManage/fundManage";
import salaryManage from "../views/admin/financialProductManage/salaryManage";
import termManage from "../views/admin/financialProductManage/termManage";
// 权限管理子页面
import adminRight from "../views/admin/rightManage/adminRight";
import userRight from "../views/admin/rightManage/userRight";
// 用户管理子页面
import bankCard from "../views/admin/userManage/bankCard";
import userCredit from "../views/admin/userManage/userCredit";
import userInfo from "../views/admin/userManage/userInfo";
// 网贷审核子页面
import loanAudit from "../views/admin/loanManage/loanAudit";
import loanInfo from "../views/admin/loanManage/loanInfo";

// 用户页面
import userHome from "../views/user/userHome";
import welcomeUser from "../views/user/welcomeUser";

Vue.use(VueRouter)

const routes = [
    {
        path: "/",
        redirect: '/Login'
    },
    {
        path: "/Login",
        component: Login
    },
    {
        path: "/Register",
        component: Register
    },
    {
        path: "/adminHome",
        component: adminHome,
        children: [
            {path: "/welcomeAdmin", component: welcomeAdmin},
            {path: "/bankManage", component: bankManage},
            {path: "/changeManage", component: changeManage},
            {path: "/fundManage", component: fundManage},
            {path: "/salaryManage", component: salaryManage},
            {path: "/termManage", component: termManage},
            {path: "/adminRight", component: adminRight},
            {path: "/userRight", component: userRight},
            {path: "/bankCard", component: bankCard},
            {path: "/userCredit", component: userCredit},
            {path: "/userInfo", component: userInfo},
            {path: "/loanAudit", component: loanAudit},
            {path: "/loanInfo", component: loanInfo},
        ]
    },
    {
        path: "/userHome",
        component: userHome,
        redirect: "/welcomeUser",
        children: [
            {path: "/welcomeUser", component: welcomeUser},

        ]
    },

];

const router = new VueRouter({routes});

