import axios from 'axios'
import {Message} from 'element-ui'
// 配置路径前缀
axios.defaults.baseURL = 'http://' + document.location.host;
axios.defaults.timeout = 5000;

// 配置前置拦截
axios.interceptors.request.use(config => {
    config.headers['authorization'] = window.sessionStorage.getItem('token');
    return config;
})

// 配置后置拦截
axios.interceptors.response.use(response => {
    let data = response.data;
    switch (data.code) {
        case 'SUCCESS':
            console.log("请求成功");
            break;
        case 'FAILURE':
            console.log("请求失败");
            break;
        case 'ERROR':
            Message.error("抱歉，我们遇到了错误,请求终止：" + data.reason);
            return new Error("error");
        default:
            break;
    }
    return response;
});

function GET(config,callback) {
    axios({
        url: config.url,
        method: 'get',
        params: config.params ? config.params : undefined,
        headers: config.headers ? config.headers : undefined,
    }).then(res => {
        console.log("接收到服务器响应：");
        console.log(res);
        callback(res);
    }).catch(err => {
        Message.error("在与服务器通讯的过程发生了错误");
        console.log("在与服务器通讯的过程中捕获到错误：" + err);
    })
}

function POST(config,callback) {
    axios({
        url: config.url,
        method: 'post',
        params: config.params ? config.params : undefined,
        headers: config.headers ? config.headers : undefined,
        data: config.data,
    }).then(res => {
        console.log("接收到服务器响应：");
        console.log(res);
        callback(res);
    }).catch(err => {
        Message.error("在与服务器通讯的过程发生了错误");
        console.log("在与服务器通讯的过程中捕获到错误：" + err);
    })
}

export default {
    GET, POST,
}