<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/welcomeAdmin' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户信息管理</el-breadcrumb-item>
            <el-breadcrumb-item>用户在线状态</el-breadcrumb-item>
        </el-breadcrumb><br />
        <el-card class="box-card">
            <el-row>
                <el-col :span="10">
                    <el-input placeholder="搜索" v-model="inputSearch.str" class="el-input1">
                        <el-select v-model="inputSearch.type" slot="prepend" placeholder="请选择" style="width: 110px;">
                            <el-option label="用户名" value="username"></el-option>
                            <el-option label="真实姓名" value="realname"></el-option>
                            <el-option label="手机号" value="phone"></el-option>
                            <el-option label="邮箱" value="email"></el-option>
                        </el-select>
                        <el-button slot="append" icon="el-icon-search" @click="Button_Search"></el-button>
                    </el-input>
                </el-col>
            </el-row><br />
<!--            表格-->
            <el-table border stripe :data="table.tableData">
                <el-table-column align="center" prop="id"          label="ID"></el-table-column>
                <el-table-column align="center" prop="username"    label="用户名"></el-table-column>
                <el-table-column align="center" prop="realname"    label="真实姓名"></el-table-column>
                <el-table-column align="center" prop="phone"       label="手机号"></el-table-column>
                <el-table-column align="center" prop="email"       label="邮箱"></el-table-column>
                <el-table-column align="center" prop="status"      label="用户状态"></el-table-column>
                <el-table-column align="center" label="相关操作">
                    <template slot-scope="scope">
                        <el-button size="mini" type="danger"    @click="Button_GoOut(scope.$index, scope.row)">强制下线</el-button>
                    </template>
                </el-table-column>
            </el-table><br />
<!--            分页标签栏-->
            <el-pagination
                    @size-change="paginationChange"
                    @current-change="paginationCurrentChange"
                    :current-page="table.page.currentPage"
                    :page-sizes="table.page.pageSizes"
                    :page-size="table.page.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="table.page.total">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
    export default {
        name: "userInfo",
        data() {
            return {
                inputSearch: {
                    str: '',
                    type: null,
                },
                table: {
                    tableData: [
                        {
                            id: 1,
                            username: '张三',
                            realname: '张三',
                            phone: '151****3882',
                            email: '123@qq.com',
                            status: '离线',
                        },
                        {
                            id: 1,
                            username: '张三',
                            realname: '张三',
                            phone: '151****3882',
                            email: '123@qq.com',
                            status: '离线',
                        },
                        {
                            id: 1,
                            username: '张三',
                            realname: '张三',
                            phone: '151****3882',
                            email: '123@qq.com',
                            status: '离线',
                        },
                    ],
                    page: {
                        maxID: 0,
                        currentPage: 1,                     // 当前页数
                        pageSizes: [5, 7, 9, 11, 20],       // 每页显示个数选择器的选项设置
                        pageSize: 5,                        // 每页显示条目个数，支持 .sync 修饰符
                        total: 50,                          // 条目总数
                    },
                },
            }
        },
        created() {
            let self = this;
            self.$request.GET({
                url: 'admin/userManage/userInfo/userList',
                params: {
                    startID: self.table.page.maxID,
                    number: self.table.page.pageSize,
                },
            },res => {
                let data = res.data;
                if (data.code === 'SUCCESS') {
                    self.table.tableData = data.extend.userList;
                    self.table.page.total = data.extend.userNumber;
                    // 计算最大ID
                    for (let i in self.tableData) {
                        self.page.maxID = Math.max(self.page.maxID,self.tableData[i].id);
                    }
                }else if (data.code === 'FAILURE') {
                    self.$message({
                        type: 'error',
                        message: '请求用户列表失败，原因：' + data.extend.reason,
                    });
                }
            });
        },
        methods: {
            // 搜索按钮
            Button_Search() {                   // TODO 搜索功能待完善
                this.$message({
                    type: 'info',
                    message: '你按下了搜索按钮',
                });

            },
            // 强制下线按钮
            Button_GoOut(index,row) {           // TODO 强制下线功能未完成
                if (row.status === '离线') {
                    this.$message({
                        type: 'info',
                        message: '该用户并未上线!'
                    });
                    return;
                }
                this.$MessageBox.confirm('你将强制“' + row.username + '”下线, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true,
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '操作已提交，正在通知用户!'
                    });
                    // this.$request1({
                    //     url: 'home/return',
                    //     method: 'get',
                    //     params:{
                    //         type: 'goBack_Res',
                    //         User_ID:window.$user.User_ID,
                    //         Res_ID: row.Res_ID,
                    //     },
                    // }).then((res) => {
                    //     console.log('接收到服务器发来的数据：' + res.data);
                    //     if (res.data === 'YES') {
                    //         this.$message({
                    //             type: 'success',
                    //             message: '归还中，等待书籍主人确认!'
                    //         });
                    //         this.TimeIsChange();
                    //     }else {
                    //         this.$message({
                    //             type: 'error',
                    //             message: '操作失败，原因：' + res.data,
                    //         });
                    //     }
                    // }).catch((err) => {
                    //     this.$message({
                    //         type: 'error',
                    //         message: "与服务器通讯的过程中发生异常：" + err,
                    //     });
                    //     console.log("与服务器通讯的过程中发生异常：" + err);
                    // });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '操作已撤销'
                    });
                });
            },
            paginationChange(val) {
                this.$message({
                    type: 'info',
                    message: 'paginationChange函数被触发，' + val,
                });
            },
            paginationCurrentChange(val) {
                this.$message({
                    type: 'info',
                    message: 'paginationCurrentChange函数被触发，' + val,
                });
            }
        }
    }
</script>

<style lang="less" scoped>
    .baseStyle() {
        background-color: rgba(233,255,200,0.3);
    }
    .el-breadcrumb{

    }
    .el-card {
        .baseStyle();
        .el-input1 {
            width: 500px;
            .el-select {
                .baseStyle();
            }
            .el-button {
                background-color: rgba(255,255,255,0.5);
            }
        }
    }
</style>